// Importar explícitamente los estilos SCSS
import './sass/style.scss';

console.log('Hello Vite!');

// Menú hamburguesa
document.addEventListener('DOMContentLoaded', () => {
  const hamburger = document.querySelector('.header__nav__hamburger');
  const navMenu = document.querySelector('.header__nav ul');
  const overlay = document.querySelector('.header__nav__overlay');
  
  // Comprobar si los elementos existen antes de manipularlos
  if (!hamburger || !navMenu) {
    console.error('No se encontraron elementos requeridos para el menú de navegación');
    return;
  }
  
  // Asegurar que el menú esté cerrado al cargar la página
  navMenu.classList.remove('active');
  navMenu.style.right = '-100%';
  
  if (overlay) {
    overlay.classList.remove('active');
  }
  
  // Manejar el clic en el botón hamburguesa
  hamburger.addEventListener('click', () => {
    hamburger.classList.toggle('active');
    navMenu.classList.toggle('active');
    
    if (overlay) overlay.classList.toggle('active');
    
    // Cambiar manualmente el estilo right
    if (navMenu.classList.contains('active')) {
      navMenu.style.right = '0';
    } else {
      navMenu.style.right = '-100%';
    }
  });
  
  // Cerrar el menú al hacer clic en el overlay
  if (overlay) {
    overlay.addEventListener('click', () => {
      hamburger.classList.remove('active');
      navMenu.classList.remove('active');
      overlay.classList.remove('active');
      navMenu.style.right = '-100%';
    });
  }
  
  // Cerrar el menú al hacer clic en un enlace
  const navLinks = document.querySelectorAll('.header__nav ul li a');
  
  navLinks.forEach(link => {
    link.addEventListener('click', () => {
      hamburger.classList.remove('active');
      navMenu.classList.remove('active');
      if (overlay) overlay.classList.remove('active');
      navMenu.style.right = '-100%';
    });
  });
});

